@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}


.App {
  text-align: center;
  background: #dd0e04;
  min-height: 1000vh;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  transition: 0.6s;
  padding: 40px 100px;
  z-index: 1;
}

.header.sticky {
  padding: 10px 100px !important;
  background: white;
}

.header .logo {
  position: relative;
  font-weight: 700;
  color: #fff;
  text-decoration: none;
  font-size: 2em;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: 0.6s;
}

.header ul {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.header ul li {
  position: relative;
  list-style: none;
}

.header ul li a {
  position: relative;
  margin: 0 15px;
  text-decoration: none;
  color: #fff;
  letter-spacing: 2px;
  font-weight: 500px;
  transition: 0.6s; 
}

.banner{
  position: relative;
  width: 100%;
  height: 120vh;
  background : url(/Nav-Scroll/static/media/back2.534ececb.jpg);
  /* background: url('./back3.jpg') no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover; */
  background-size: cover;
  /* background-attachment: fixed; */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-clip-path: circle(250vh at 35% -150vh);
          clip-path: circle(250vh at 35% -150vh);
}

.header.sticky .logo{
  color: black;
}

.header.sticky  ul li a {
  color: black;
}


.wrapper{
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-align-items: center;
          align-items: center;
  /* border: 5px solid black; */
}
.wrapper .static-txt{
  color: black;
  font-size: 60px;
  font-weight: 500;
}
.wrapper .dynamic-txts{
  margin-left: 15px;
  height: 90px;
  /* line-height: 90px; */
  overflow: hidden;
}
.dynamic-txts li{
  list-style: none;
  color: white;
  font-size: 60px;
  font-weight: 500;
  position: relative;
  top: 0;
  -webkit-animation: slide 12s steps(4) infinite;
          animation: slide 12s steps(4) infinite;
  text-align-last: start;
}
@-webkit-keyframes slide {
  100%{
    top: -360px;
  }
}
@keyframes slide {
  100%{
    top: -360px;
  }
}
.dynamic-txts li span{
  position: relative;
  margin: 5px 0;
  line-height: 90px;
}
.dynamic-txts li span::after{
  content: "";
  position: absolute;
  left: 0;
  height: 100%;
  width: 100%;
  background: #dd0e04;
  border-left: 2px solid white;
  -webkit-animation: typing 3s steps(15) infinite;
          animation: typing 3s steps(15) infinite;
}
@-webkit-keyframes typing {
  40%, 60%{
    left: calc(100% + 30px);
  }
  100%{
    left: 0;
  }
}
@keyframes typing {
  40%, 60%{
    left: calc(100% + 30px);
  }
  100%{
    left: 0;
  }
}


